module $ { export class $mol_stringer extends $mol_viewer {

	/// tagName \input
	tagName() {
		return "input"
	}

	/// enabled true
	enabled() {
		return true
	}

	/// hint \
	hint() {
		return ""
	}

	/// type \text
	type() {
		return "text"
	}

	/// attr * 
	/// 	placeholder < hint 
	/// 	type < type
	attr() {
		return $mol_merge_dict( super.attr() , {
			"placeholder" : ()=> <any> this.hint() ,
			"type" : ()=> <any> this.type() ,
		} )
	}

	/// disabled false
	disabled() {
		return false
	}

	/// value \
	@ $mol_prop()
	value( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : ""
	}

	/// valueChanged > value
	valueChanged( ...diff : any[] ) {
		return this.value(  ...diff )
	}

	/// field * 
	/// 	disabled < disabled 
	/// 	value < valueChanged
	field() {
		return $mol_merge_dict( super.field() , {
			"disabled" : ()=> <any> this.disabled() ,
			"value" : ()=> <any> this.valueChanged() ,
		} )
	}

	/// eventChange null
	@ $mol_prop()
	eventChange( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// event * input > eventChange
	event() {
		return $mol_merge_dict( super.event() , {
			"input" : ( ...diff : any[] )=> <any> this.eventChange(  ...diff ) ,
		} )
	}

} }

