module $ { export class $mol_scroller extends $mol_viewer {

	/// scrollTop 0
	scrollTop() {
		return 0
	}

	/// scrollLeft 0
	scrollLeft() {
		return 0
	}

	/// field * 
	/// 	scrollTop < scrollTop 
	/// 	scrollLeft < scrollLeft
	field() {
		return $mol_merge_dict( super.field() , {
			"scrollTop" : ()=> <any> this.scrollTop() ,
			"scrollLeft" : ()=> <any> this.scrollLeft() ,
		} )
	}

	/// eventScroll null
	@ $mol_prop()
	eventScroll( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// event * 
	/// 	scroll > eventScroll 
	/// 	overflow > eventScroll 
	/// 	underflow > eventScroll
	event() {
		return $mol_merge_dict( super.event() , {
			"scroll" : ( ...diff : any[] )=> <any> this.eventScroll(  ...diff ) ,
			"overflow" : ( ...diff : any[] )=> <any> this.eventScroll(  ...diff ) ,
			"underflow" : ( ...diff : any[] )=> <any> this.eventScroll(  ...diff ) ,
		} )
	}

} }

