module $ { export class $mol_clicker extends $mol_viewer {

	/// tagName \button
	tagName() {
		return "button"
	}

	/// enabled true
	enabled() {
		return true
	}

	/// eventClick null
	@ $mol_prop()
	eventClick( ...diff : any[] ) {
		return ( diff[0] !== void 0 ) ? diff[0] : <any> null
	}

	/// event * click > eventClick
	event() {
		return $mol_merge_dict( super.event() , {
			"click" : ( ...diff : any[] )=> <any> this.eventClick(  ...diff ) ,
		} )
	}

	/// disabled false
	disabled() {
		return false
	}

	/// attr * disabled < disabled
	attr() {
		return $mol_merge_dict( super.attr() , {
			"disabled" : ()=> <any> this.disabled() ,
		} )
	}

} }

